<template>
            <div class="centerx example-loading" v-if="LOADING">
                <div class="fill-row-loading">
                    <div id="loading-point2" class="vs-con-loading__container loading-example"> </div>
                </div>
            </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
    computed: {
         ...mapGetters({
            LOADING: 'contentLoaderMain'
        })
    },
    mounted() {
        this.$vs.loading({
            container: "#loading-point2",
            type:"point",
        });
    },
}
</script>
<style lang="scss">
    .centerx{
            height:100%;
            display: flex;
            justify-content: center;
        }
        .fill-row-loading{
            display:flex;
            align-items:center;
        }
        .loading-example{
            width:120px;
            float:left;
            height:120px;
